@font-face {
  font-family: "RobotoCondensed";
  font-display: swap;
  src:url("../fonts/RobotoCondensed-Regular.woff2")
    format("woff2"),
    url("../fonts/RobotoCondensed-Regular.woff")
    format("woff");
  font-weight: 400;
  font-style: normal;
}

